/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif, Arial, Helvetica, sans-serif;
}
@media (min-width: 768px) {
  html {
    font-size: 42.5%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

* {
  font-size: 1.7rem;
}

body {
  margin-top: 7rem;
}

.contenedor {
  width: 94%;
  margin: 0 auto;
  max-width: 1200px;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
}

h2 {
  font-size: 3rem;
  font-weight: 600;
}

h3 {
  font-size: 2rem;
  font-weight: 600;
}

h4 {
  font-size: 1.8rem;
  font-weight: 400;
}

p {
  font-weight: 400;
  color: #202020;
}

span {
  font-weight: 600;
}

img {
  width: 100%;
}

ul {
  list-style: none;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.centrar-seccion {
  margin: 0 auto;
  width: 80%;
  max-width: 1200px;
}
@media (min-width: 768px) {
  .centrar-seccion {
    width: 50%;
  }
}

.padding-seccion {
  padding: 2rem 0;
}

.title {
  color: #0099c0;
  text-align: center;
}

.centrar-texto {
  justify-content: center;
}

.brand {
  display: flex;
  align-items: center;
}

.p-top {
  padding-top: 3rem;
}

@media (min-width: 768px) {
  .info-image {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.fill-display {
  display: grid;
  place-items: center;
  min-height: 100vh;
}

.btn-verde {
  background-color: #06e16f;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline;
  border: none;
  font-size: 2rem;
  transition: all 0.3s ease-out;
}
.btn-verde:hover {
  background-color: #05c863;
  cursor: pointer;
}

.btn-rojo {
  background-color: #ff504d;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline;
  border: none;
  font-size: 2rem;
  transition: all 0.3s ease-out;
}
.btn-rojo:hover {
  background-color: #ff3734;
  cursor: pointer;
}

.btn-naranja {
  background-color: #ff961f;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline;
  border: none;
  font-size: 2rem;
  transition: all 0.3s ease-out;
}
.btn-naranja:hover {
  background-color: #ff8a06;
  cursor: pointer;
}

.btn-azul {
  background-color: #005cb5;
  color: #fff;
  font-weight: 400;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline;
  border: none;
  font-size: 2rem;
  transition: all 0.3s ease-out;
}
.btn-azul:hover {
  background-color: #004f9c;
  cursor: pointer;
}

.formulario label {
  display: block;
  margin: 2rem 0;
}
.formulario textarea,
.formulario input:not([type=submit]) {
  border: none;
  border-bottom: 0.4rem solid #005cb5;
  padding: 1.5rem 1rem 1rem 1rem;
  resize: none;
  outline: none;
  width: 100%;
}
.formulario select {
  display: block;
}
.formulario textarea {
  height: 10rem;
}

.navegacion-principal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  background-color: #0099c0;
  padding: 1.2rem 0;
  -webkit-box-shadow: 0px 20px 25px -9px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 20px 25px -9px rgba(0, 0, 0, 0.4);
}
.navegacion-principal h2 {
  margin: 0;
  color: #fff;
}
.navegacion-principal a {
  color: #fff;
}
.navegacion-principal .contenedor {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navegacion-botones {
  display: flex;
}
.navegacion-botones li {
  margin: 0 0.5rem;
}
@media (min-width: 768px) {
  .navegacion-botones li {
    margin: 0 2rem;
  }
}
.navegacion-botones li:last-child {
  margin-right: 0;
}

.top-button {
  position: fixed;
  z-index: 1;
  bottom: 0;
  right: 0;
  padding: 2rem 2.4rem;
  margin: 0 2rem 2rem 0;
  color: #fff;
  font-size: 2rem;
  border-radius: 50%;
  background-color: #005cb5;
}

.slider-show {
  position: relative;
  overflow: hidden;
  width: 100%;
}
.slider-show .slider {
  display: flex;
  width: 300%;
}
.slider-show a {
  color: #fff;
}

.slide {
  width: 100%;
  height: 30rem;
  animation: slider 20s infinite alternate linear;
}
.slide img {
  position: absolute;
  height: 30rem;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}
.slide .slide-info {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: right;
}
.slide .slide-info h2, .slide .slide-info h3, .slide .slide-info h4 {
  margin: 0;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.6);
}

@keyframes slider {
  0% {
    transform: translate(0);
  }
  33% {
    transform: translate(0);
  }
  36% {
    transform: translate(-100%);
  }
  66% {
    transform: translate(-100%);
  }
  69% {
    transform: translate(-200%);
  }
  100% {
    transform: translate(-200%);
  }
}
.main-noticias h1 {
  font-size: 2.5rem;
  margin: 0;
}

@media (min-width: 768px) {
  .noticias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .noticias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.noticia {
  width: 100%;
  margin: 2rem 0;
  -webkit-box-shadow: -1px 11px 25px -1px rgba(0, 0, 0, 0.43);
  box-shadow: -1px 11px 25px -1px rgba(0, 0, 0, 0.43);
  transition: all 0.3s ease-out;
}
@media (min-width: 768px) {
  .noticia {
    margin: 0;
  }
}
.noticia .thumb {
  height: 21rem;
  width: 100%;
}
.noticia img {
  height: 21rem;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}
.noticia a {
  color: #fff;
}
.noticia:hover {
  transform: scale(105%);
}

.noticia-info {
  background-color: #e2e2e2;
  padding: 1rem 2rem;
}
.noticia-info .btn-azul {
  color: #fff;
}
.noticia-info a {
  color: #0099c0;
}
.noticia-info a h4 {
  display: inline;
}
.noticia-info h3, .noticia-info h4, .noticia-info span {
  margin: 0;
  padding: 0.4rem 0;
}
.noticia-info span {
  color: #005cb5;
}

.footer-principal {
  background-color: #4c4954;
  margin: 0;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .footer-principal .contenedor {
    display: grid;
    grid-template-columns: 3fr 1fr;
  }
}
.footer-principal h2 {
  margin: 0;
  display: inline;
  color: #fff;
}
.footer-principal a {
  color: #fff;
}
.footer-principal li {
  margin: 2rem 0;
}
.footer-principal p {
  color: #fff;
  text-align: center;
}
.footer-principal p a {
  color: #005cb5;
}

@media (min-width: 768px) {
  .paises {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .paises {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.paises .pais {
  -webkit-box-shadow: -1px 17px 27px 4px rgba(0, 0, 0, 0.2);
  box-shadow: -1px 17px 27px 4px rgba(0, 0, 0, 0.2);
  padding: 1rem 3rem 3rem 3rem;
  margin: 4rem 0;
  transition: all 0.3s ease-out;
}
.paises .pais:hover {
  transform: scale(105%);
}
@media (min-width: 768px) {
  .paises .pais {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .paises .pais .thumb {
    height: 29rem;
  }
}
.paises .pais .thumb img {
  object-fit: cover;
  object-position: center center;
}
@media (min-width: 768px) {
  .paises .pais .thumb img {
    height: 29rem;
  }
}
.paises .pais p {
  font-size: 3rem;
}

@media (min-width: 768px) {
  .autores {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .autores {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.autores .autor {
  padding: 1rem 3rem;
  margin-bottom: 2rem;
  -webkit-box-shadow: -1px 17px 27px 4px rgba(0, 0, 0, 0.2);
  box-shadow: -1px 17px 27px 4px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-out;
}
.autores .autor:hover {
  transform: scale(105%);
}
.autores .autor .nombre {
  color: #005cb5;
}

.perfil {
  display: grid;
  grid-template-columns: 1fr 3fr;
  gap: 0.5rem;
  place-items: center;
}
.perfil .thumb {
  height: 6rem;
  border-radius: 50%;
  overflow: hidden;
  -webkit-box-shadow: -1px 17px 27px 4px rgba(0, 0, 0, 0.2);
  box-shadow: -1px 17px 27px 4px rgba(0, 0, 0, 0.2);
}
.perfil .thumb img {
  height: 6rem;
  object-fit: cover;
  object-position: center center;
}